:root {
    --mainColor: rgb(186, 203, 205);
    --minorColor: LightGray;
    --contraColor: #1344a0;
    --mainBG: White;
    --minorBG: WhiteSmoke;
    --shadowBG: #ddddff;

    /* status colors */
    --changeStat: orange;
    --errorStat: red;
    --successStat: #55A62A;

    /* text */
    --mainTxtColor: #202020;
    --mainTxtSize: 19px;
    --mainTxtFont: Georgia1, Georgia, serif;
    --minorTxtSize: 16px;

}

.minorTxt{
    font-size: var(--minorTxtSize);
}
p,span,input,select,div {
    /*#202020;*/
    font-size: 19px;
    font-family: Georgia1, Georgia, serif;
    color: #353535;
}
aut{font-variant:small-caps}

/* links */
a {
    text-decoration: none;
    color: var(--contraColor);
}

a:hover {
    cursor: pointer;
}


h1, h2, h3, h4{
    color: #284b63;
}
input{
    border: none;
    outline: none;
    background-color: white;
    box-shadow: 0 1px 4px #d9d9d9;
    transition: box-shadow 0.2s;
}
input:focus{
    box-shadow: 0 1px 8px #d9d9d9;
}

input[type=text], input[type=password]{
    border-bottom: 1px solid #d9d9d9;
    color: #284b63;
    padding: 4px 8px;
}
input[type=button],input[type=submit]{
    color: #284b63;
    border-radius: 7px;
    padding: 5px 15px;
    border-radius: 0;
}
select{
    border-radius: 0;
    appearance: none;
    border: none;
    box-shadow: 0 1px 4px #d9d9d9;
    background-color: white;
    padding: 5px 10px;
}

.cat{
    position: absolute;
    top: 4%;
    left: 4%;
    opacity: 0%;
    animation-name: cat;
    animation-duration:100s;
}
@keyframes cat {
    0% {opacity: 0%;}
    90% {opacity: 0%;}
    93% {left: 4%; top: 4%; opacity: 100%; transform: rotate(0turn);}
    96% {left: 50%; transform: rotate(1turn);}
    99% {left: 96%; top: 96%; opacity: 100%; transform: rotate(2turn);}
    100% {left: 96%; top: 96%; opacity: 0%; transform: rotate(2turn);}
}

/* general */
.footerTop{
    display: flex;
    justify-content: space-evenly;
    box-shadow: 0 0 4px #dddddd;
    background-color: rgb(253, 253, 253);
    /*border-bottom: 1px solid gray;*/
}
.footerTop>*{
    margin: 8px 6px;
}

.noneLst{
    list-style-type: none;
    margin: 0;
    padding: 0;
}
/* ****************   ZETTEL   **************** */
.searchFields>select{
    box-shadow: none;
}
#navBox{
    position: sticky;
    top: 15px;
    float: right;
    z-index: 1000000;
}
#navScreen{
    display: inline-block;
    color: #5e5e5e;
    margin-right: 15px;
    box-shadow: 0 0 1px #3c6e71 ;
    border-radius: 7px;
    padding: 5.5px 12px 4.5px 12px;
    background-color: white;
}
#navPage{
    display: inline-block;
    background-color: white;
    border-radius: 7px;
    width: max-content;
    padding: 5px 10px;
    color: var(--mainColor);
    box-shadow: 0 0 1px #3c6e71 ;
    -webkit-user-select:none;
    -webkit-touch-callout:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
#navPage>*{
    cursor:pointer;
}
#navSelect{
    padding: 0 10px 0 10px;
    border-left: 1px solid rgb(186, 203, 205);
    border-right: 1px solid rgb(186, 203, 205);
    margin: 0 10px 0 10px;
    cursor: initial;
}

.zettel_box {
    display: grid;
    justify-content: space-around;
    width: 100%;
}

.zettel {
    padding: 0;
    margin: 0;
    position: relative;
    background: none;
    transition: box-shadow 0.5s ease-out;
    border-radius: 7px;
}
div.zettel.selMarked{box-shadow: 0px 0px 18px var(--shadowBG)}
.zettel_img{width: 100%}
div.zettel > img{border-radius: 7px}
.zettel_menu{
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    font-size: 18px;
    padding: 15px 12px;
    text-align: center;
    background: rgba(300,300,300,0.5);
    border-radius: 0 0 7px 7px;
}
.zettel_msg{
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 10px 10px;
    font-size: 30px;
    font-weight: bold;
    cursor: normal;
}

.no_use {background-color: var(--mainBG)}
img.no_use {filter: grayscale(100%)}

/* digital zettel */
.digitalZettel{
    /*position: absolute;*/
    display: grid;
    grid-template-columns: 25% auto 25%;
    grid-template-rows: 50px 30px auto;
    grid-gap: 10px;
    padding: 20px 30px;
    /*
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    */
    /*background-image: url('/zettel/default/0/0.jpg');*/
    background: linear-gradient(12deg, #d9c8b3,#ffeeca,#f7ecd8, #ffeeca,#f7ecd8);
    filter: grayscale(0%);
    background-size: contain;
    border-radius: 7px;
    /*overflow: hidden;*/
    min-height: 200px;
}
.digitalZettel > div{
    -webkit-user-select:auto;
    -webkit-touch-callout:auto;
    -moz-user-select:auto;
    -ms-user-select:auto;
    user-select:auto;
}
.digitalZettelLemma{
    grid-area: 1/1/1/2;
    /*
    position: absolute;
    top: 20px;
    left: 30px;
    */
}
.digitalZettelDate{
    grid-area: 1/3/1/4;
    text-align: right;
    /*
    position: absolute;
    top: 20px;
    right: 30px;
    */
}
.digitalZettelWork{
    grid-area: 2/2/2/3;
    text-align: center;
    /*
    position: absolute;
    top: 80px;
    left: 10px;
    right: 10px;
    */
}
.digitalZettelText{
    grid-area: 3/1/3/4;
    padding: 0 20px;
    text-align: justify;
    /*
    position: absolute;
    top: 140px;
    left: 30px;
    right: 30px;
    text-align: left;
    */
}




/* ####################################################
       -VI- opera
       ####################################################*/
       div.operaBox{
        /*padding: 0 20px 0 20px;*/
        display: flex;
        overflow: auto;
        flex: none;
        width: 100%;
        height: 100%;
        flex-flow: column nowrap;
        scroll-snap-type: y proximity;
    }
    div.operaBox > div{
        text-align: center;
        scroll-snap-align: start;
        flex: none;
        font-size: 128px;
        width: 100%;
        border-top: 1px dotted black;
    }
    div.operaBox >div:first-child{
        border-top: none;
    }

    div.operaBox > table{
        font-size: 13pt;
        background-color: white;
        margin: 15px 0;
        border-collapse: collapse;
        width: 100%;
    }

    div.operaBox > table > tbody > tr > td{
        padding: 5px 10px;
        vertical-align: top;
        text-align: left;
    }
    .c1{
        width: 11%;
    }
    .c2{
        border-left: 1px solid black;
        width: 11%;
    }
    .c3{
        border-left: 1px solid black;
        width: 26%;
    }
    .c4{
        border-left: 1px solid black;
        width: 26%;
        font-style: italic;
    }
    .c5{
        border-left: double;
        width: 26%;
        font-size: 80%;
    }

    .c1_min{
        width: 15%;
    }
    .c2_min{
        border-left: 1px solid black;
        width: 55%;
    }
    .c5_min{
        border-left: double;
        width: 30%;
        font-size: 80%;
    }

    div.controller{
        position: fixed;
        bottom: 0;
        right: 0;
        left: 0;
        height: 40px;
        background-color: var(--minorColor);
        font-size: 80%;
        padding: 0 10px;
        text-align: right;
        box-shadow: 0 0 4px var(--shadowBG);
    }
